.hero-section {
  background-image: url(./images/bg-image.png);
  background-size: cover;
  height: 100vh;
  overflow: auto;
  background-position-y: 50%;
  background-position-x: 30%;
}

.hero-h2 {
  color: #f1f1f1;
  margin-left: auto;
  margin-right: auto;
  display: table;
  margin-top: 7em;
  text-align: center;
  font-size: 2rem;
  line-height: 1.3;
}

.p-hero {
  color: #f1f1f1;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-size: 0.9rem;
}

.hero-text {
  margin: 1em;
}

@media screen and (min-width: 850px) {
  .p-hero {
    width: 65%;
  }
}